import React from 'react'
import Layout from '../components/Layout'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from "gatsby-plugin-image"
import { BgImage } from "gbimage-bridge"

export default function DoneerUk() {
    const { placeholderImage } = useStaticQuery(
        graphql`
          query {
            placeholderImage: file(relativePath: {eq: "Doneer.jpg"}) {
              childImageSharp {
                gatsbyImageData(
                  width: 1600
                  quality: 80
                  webpOptions: {quality: 90}  
                  formats: [AUTO, WEBP, AVIF]
                )
              }
            }
          }
          
        `
      )
      const pluginImage = getImage(placeholderImage);
    
    return (
        <Layout>
             <BgImage image={pluginImage} className="bg"
      Tag="section">
            </BgImage>
        <div className="genText">
            <p>
            If you feel drawn to our mission and wish to contribute financially, we will 
            be most grateful. A financial contribution does not only open the door for you, 
            but also for others. </p>
            <p>
            Your contribution will be used to materialize Aikyam’s thoughts and ideas.
            </p>
            <p>
            Over time we will create a centre for personal growth and a community which will 
            be open to everyone. In a harmonious and loving environment you will be able to 
            get to know yourself more deeply and to grow closer to one another. A place for 
            healing and growth, for young and old, that is what Aikyam aims at.
            </p>
            <p>
            The name Aikyam comes from Sanskrit, the oldest living Indo-European language, and 
            has been chosen with care:<br/>
            - ‘Aik’ or ‘ek’ means one <br/>
            - ‘Yam? is the seed syllable to open up the heart chakra 
            </p>
            <p className="centerText">
            Aikyam means Unity, Harmony
            </p>
            <p className="centerText">
            Please transfer your contribution to IBAN NL37TRIO0320185605 in the name of Stichting Aikyam
            </p>
            <div className="centerImage">
                <img src="namaste.png"  alt="namaste"/>
            </div>
        </div>
        </Layout>
    )
}
